            draggedItem.style.transform = '';
            draggedItem.style.transition = '';

            placeholder.replaceWith(draggedItem);

            placeholder = null;
            draggedItem = null;
            dragRect = null;

            if (animationFrame) {
                cancelAnimationFrame(animationFrame);
            }

